html {
  --header-color-text: #00acdc;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.header__logo {
  max-height: 40px;
}

.header__img {
  max-width: 40px;
  max-height: 40px;
}

.header__nav {
  align-items: center;
  display: flex;
}

.header__ul {
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.header__li {
  margin-left: 20px;
}

.header__li:first-child {
  margin: 0;
}

.header__link:hover, .header__link.is-active {
  color: var(--header-color-text);
}

.header__link.is-active:after {
  content: "";
  background-color: var(--header-color-text);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (width <= 1024px) {
  .container {
    max-width: 1024px;
    margin: 0 auto;
  }
}

